body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

html {
  overflow-x: hidden;
}

:root {
  --primary-color: #14161b;
  /* --secondary-color: #5c0924; */
  --secondary-color: #353a46;
}

img {
  max-width: 100%;
  margin: 0;
}

body {
  background-color: var(--primary-color);
  overflow-x: hidden;
  height: 100vh;
}

.app-container {
  position: relative;
  height: 100%;
  width: 100%;
  background-color: #14161b;
}

/* Header styles */

.main-logo:hover {
  cursor: pointer;
}

.header-container {
  background-color: var(--secondary-color);
  padding: 0em 1.5em;
}

.header-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 2;
}

.header-nav h2 {
  color: white;
}

.header-nav-buttons {
  width: 30%;
  display: flex;
  justify-content: flex-end;
}

.header-nav-button {
  padding: 0.5em 1em;
  font-weight: bold;
  background-color: var(--secondary-color);
  color: white;
  border: none;
  border-radius: 0.5em;
  cursor: pointer;
  margin-left: 1em;
  white-space: nowrap;
}
.header-nav-button:hover {
  text-decoration: underline;
  transition: all 0.3s ease-in;
}

.header-search-container {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.header-searchbar-container {
  display: flex;
  align-items: center;
  position: relative;
  width: 40%;
}

.header-search-bar {
  width: 100%;
  padding: 0.35em 0;
  font-size: 1.1em;
  background-color: transparent;
  color: white;
  border-radius: 0.5em;
  border: 1px solid white;
  margin: 0 auto;
  text-indent: 0.5em;
}

.searchbar-icons {
  display: flex;
  align-items: center;
}

.clear-icon {
  max-width: 30px;
  position: absolute;
  right: 35px;
  filter: invert(99%) sepia(0%) saturate(3667%) hue-rotate(206deg)
    brightness(116%) contrast(97%);
}
.clear-icon:hover {
  cursor: pointer;
}

.search-icon {
  position: absolute;
  width: 18px;
  right: 10px;
  filter: invert(99%) sepia(0%) saturate(3667%) hue-rotate(206deg)
    brightness(116%) contrast(97%);
}
.search-icon:hover {
  cursor: pointer;
}

.header-search-button {
  position: absolute;
  right: 0;
  padding: 0.25em 0.5em;
  border-radius: 0.3em;
  font-weight: bold;
  border: 0.5px solid;
  cursor: pointer;
  background: transparent;
  border: none;
}

/* mobile nav */

.hamburger-icon {
  position: relative;
  width: 25px;
  z-index: 5;
  cursor: pointer;
}

/* movies-container */

.movies-container {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 2em auto;
  background-color: #14161b;
}

.movie-container {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 177.25px;
  word-wrap: break-word;
  overflow: hidden;
  cursor: pointer;
  margin: 1em;
  color: white;
  animation: popup 0.6s;
}

.movie-poster,
.movie-poster-dt {
  width: 177.25px;
  height: 266.35px;
  align-self: center;
}

.movie-poster-dt {
  border-radius: 0.5em;
}

.poster-container {
  border-radius: 0.5em;
  overflow: hidden;
}

.hovered {
  transform: scale(1.1);
  opacity: 0.6;
  transition: all 0.2s;
}

.hide {
  display: none;
}

.show-more-details {
  position: absolute;
  bottom: 50%;
  right: 37%;
  filter: invert(99%) sepia(0%) saturate(3667%) hue-rotate(206deg)
    brightness(116%) contrast(97%);
  font-weight: bold;
  z-index: 2;
  /* box-shadow: 0px 0px 5px lightgrey; */
}
/* 
.more-info {
  position: absolute;
  bottom: 50%;
  right: 25%;
  filter: invert(99%) sepia(0%) saturate(3667%) hue-rotate(206deg)
    brightness(116%) contrast(97%);
} */

.movie-title {
  margin-top: 0.5em;
}

.rating-icon {
  width: 15px;
  margin-right: 0.5em;
}

.movie-rating {
  margin-top: 0.5em;
  display: block;
  margin-left: 0.4em;
  font-weight: bold;
}

/* Movies details */

.show-movie-details {
  position: absolute;
  min-height: 100%;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.6);
  animation: popup 0.6s;
}

@keyframes popup {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.movieDetails-container {
  position: fixed;
  left: 10%;
  right: 10%;
  top: 20%;
  max-width: 80%;
  height: 70%;
  z-index: 200;
  border: 1px solid var(--secondary-color);
  border-radius: 0.3em;
  box-shadow: 5px 5px 5px var(--secondary-color);
  padding: 1em;
  background-color: var(--primary-color);
  overflow-y: scroll;
  color: white;
}

.movieDetails-video {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2em;
  width: 100%;
}

.movieDetails-video iframe {
  border: 1px solid var(--primary-color);
}

.movieDetails-title-img {
  display: flex;
}

.movie-poster-dt {
  margin: 1.2em 1.5em 0 0;
  object-fit: cover;
  align-self: flex-start;
}

.movie-details-title {
  font-size: 1.5em;
  margin-right: 1em;
  font-weight: bold;
}

.add-watchlist {
  padding: 0.5em 1.5em;
  color: var(--primary-color);
  border: none;
  border-radius: 0.3em;
  font-size: 0.9em;
}

.add-watchlist:hover {
  cursor: pointer;
}

.cast-header {
  margin-left: 1em;
}

.cast {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 0.5em;
}

.cast p {
  margin: 0.3em;
}

.cast-img {
  max-width: 90px;
  height: 134.75px;
  border-radius: 0.4em;
  object-fit: cover;
}

.cast-name {
  font-weight: bold;
  font-size: 0.8em;
  text-align: center;
}

.cast-character {
  font-size: 0.8em;
  text-align: center;
}

.movieDetails-cast {
  display: flex;
  overflow: scroll;
  overflow-y: hidden;
}

.close-icon {
  max-width: 30px;
  float: right;
  cursor: pointer;
  filter: invert(99%) sepia(0%) saturate(3667%) hue-rotate(206deg)
    brightness(116%) contrast(97%);
}

/* Movies details end */

#no-movies,
#movies-found {
  padding-top: 1em;
  text-align: center;
  font-size: 1rem;
  color: white;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

#no-movies {
  padding-top: 4em;
}

.mobile-nav-btn {
  background: transparent;
  color: white;
  border: none;
  margin-top: 2em;
  font-weight: bold;
  cursor: pointer;
}
.mobile-nav-btn:hover {
  text-decoration: underline;
  transform: all 0.2s ease-in;
}

.mobile-nav-show {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  height: 100vh;
  background-color: #282c36;
  position: absolute;
  width: 110px;
  top: 0;
  right: 0;
  padding-top: 5em;
  color: white;
  z-index: 2;
  transition: all 0.3s;
}

.mobile-nav-hide {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  height: 100vh;
  background-color: #282c36;
  position: absolute;
  width: 110px;
  top: 0;
  right: -100%;
  padding-top: 5em;
  color: white;
  z-index: 2;
  transition: all 0.3s;
}

@media (max-width: 1120px) {
  .header-nav-buttons {
    width: 50%;
  }
  /* .movies-container {
    max-width: 980px;
  } */

  .movies-container {
    padding: 0;
  }
}

@media (max-width: 935px) {
  .header-nav h2 {
    font-size: 1.3rem;
  }
  .header-nav-buttons {
    width: 50%;
    font-size: 0.8rem;
  }
  /* .movies-container {
    max-width: 750px;
    grid-template-columns: 1fr 1fr 1fr;
  } */
}

@media (max-width: 825px) {
  .header-nav-buttons {
    width: 50%;
  }
  .header-container {
    padding: 0 1.25em;
  }
}

@media (max-width: 740px) {
  .header-nav-buttons {
    width: 50%;
    font-size: 0.5rem;
  }
  .movieDetails-title-img div {
    font-size: 0.9em;
  }
  .movies-container {
    padding: 0;
  }
}

@media (max-width: 700px) {
  .header-nav-buttons {
    display: none;
  }
  .movieDetails-title-img div {
    font-size: 0.85em;
  }
  .header-container {
    padding: 0 1em;
  }
}

@media (max-width: 600px) {
  .movieDetails-title-img {
    display: flex;
    flex-direction: column;
    font-size: 1.2em;
  }
  .header-searchbar-container {
    width: 60%;
  }
}
